/** 常用字体大小 **/
* {
  margin: 0;
  padding: 0;
}
.container {
  display: flex;
}
.container .nav-left {
  min-width: 180px;
  background-color: #001529;
  color: #ffffff;
  height: calc(100vh);
}
.container .main {
  flex: 1;
  min-width: 1000px;
  min-height: 620px;
  height: calc(100vh);
  background-color: #f1f3f5;
  overflow: auto;
}
.container .content {
  position: relative;
  padding: 20px;
}
